<!--<script src="../../build/vue-loader.conf.js"></script>-->
<template>
    <div class="index-wrap">
      <div class="index-left">
        <div class="index-left-block">
          <h2>全部产品</h2>

          <!--循环-->
          <template v-for="product in productList">
            <h3>{{ product.title }}</h3>
            <ul>
              <li v-for="item in product.list">
                <a :href="item.url" target="_blank">{{ item.name }}</a>
                <span v-if="item.hot" class="hot-tag">HOT</span>
              </li>
            </ul>
            <div v-if="!product.last" class="hr"></div>
          </template>

        </div>
        <div class="index-left-block lastest-news">
          <h2>友情链接</h2>
          <ul>
            <li v-for="item in newsList">
              <a class="new-item" :href="item.url"  target="_blank">{{ item.title }}</a>
            </li>
          </ul>
        </div>
      </div>

      <div class="index-right">
        <slide-show :slides="slides" :intv="invTime" @onChange="doSomethingOnSlideShow"></slide-show>
        <div class="index-board-list">
          <div
            class="index-board-item"
            v-for="(item,index) in boardList"
            :class="[{'line-last':index % 2 !== 0},'index-board-' + item.id]">
            <div class="index-board-item-inner" >
              <h2>{{ item.title }}</h2>
              <p>{{ item.description }}</p>
              <div class="index-board-button">
                <router-link :to="{path:'/detail/' + item.toKey}" class="button">立即购买</router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    import slideShow from '../components/slideShow'
    import bus from '../eventsCenter/bus'
    export default {
      components:{
        slideShow
      },
      created() {
        this.$http.get('api/getNewsList')
          .then( (res) => {         //成功
            console.log(res.data.getNewsList);
            this.newsList = res.data.getNewsList;
          },(err) => {              //失败getNewsList
            console.log(err)
          })
      },
      data() {
        return {
          invTime: 3000,
          slides: [
            {
              src: require('../assets/slideShow/pic1.jpg'),
              title: 'xxx1',
              href: '/detail/analysis'
            },
            {
              src: require('../assets/slideShow/pic2.jpg'),
              title: 'xxx2',
              href: '/detail/count'
            },
            {
              src: require('../assets/slideShow/pic3.jpg'),
              title: 'xxx3',
              href: '/detail/publish'
            },
            {
              src: require('../assets/slideShow/pic4.jpg'),
              title: 'xxx4',
              href: '/detail/forecast'
            }
          ],
          productList:{
            pc:{
              title:'暴雪产品',
              list:[
                {
                  name: '星际争霸重置版',
                  url: 'http://starcraft.com'
                },
                {
                  name: '魔兽世界',
                  url: 'https://www.wowchina.com/zh-cn/'
                },
                {
                  name: '星际争霸2',
                  url: 'http://sc2.blizzard.cn/',
                  hot: true
                },
                {
                  name: '守望先锋',
                  url: 'http://ow.blizzard.cn/home'
                }
              ]
            },
            app:{
              title:'生活应用类',
              last:true,
              list:[
                {
                  name: '微信',
                  url: 'http://weixin.com'
                },
                {
                  name: '淘宝',
                  url: 'https://www.taobao.com/',
                  hot: true
                },
                {
                  name: '京东',
                  url: 'https://www.jd.com/'
                },
                {
                  name: '美团',
                  url: 'https://www.meituan.com'
                }
              ]
            }
          },
          newsList:[],
          boardList: [
            {
              title: '开放产品',
              description: '开放产品是一款开放产品',
              id: 'car',
              toKey: 'count',
              saleout: false
            },
            {
              title: '品牌营销',
              description: '品牌营销帮助你的产品更好地找到定位',
              id: 'earth',
              toKey: 'analysis',
              saleout: false
            },
            {
              title: '使命必达',
              description: '使命必达快速迭代永远保持最前端的速度',
              id: 'loud',
              toKey: 'forecast',
              saleout: true
            },
            {
              title: '勇攀高峰',
              description: '帮你勇闯高峰，到达事业的顶峰',
              id: 'hill',
              toKey: 'publish',
              saleout: false
            }
          ]
        }
      },
      methods:{
        doSomethingOnSlideShow(index) {
//          console.log('doSomethingOnSlideShow' + index)
        }
      },
      mounted() {
        if(this.newsList.length == 0){
          this.newsList =[
            {"id":1,"title":"腾讯网腾讯网腾讯网","url":"http://www.qq.com/"},
            {"id":2,"title":"网易163网易163网易163","url":"http://www.163.com/"},
            {"id":3,"title":"凤凰网凤凰网","url":"http://www.ifeng.com/"},
            {"id":4,"title":"搜狐网","url":"http://www.sohu.com/"}
          ]
        }
      }
    }
</script>

<style scoped>
  .index-wrap {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
  }
  .index-left {
    float: left;
    width: 300px;
    text-align: left;
  }
  .index-right {
    float: left;
    width: 900px;
  }
  .index-left-block {
    margin: 15px;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
  }
  .index-left-block .hr {
    margin-bottom: 20px;
  }
  .index-left-block h2 {
    background: #4fc08d;
    color: #fff;
    padding: 10px 15px;
    margin-bottom: 20px;
  }
  .index-left-block h3 {
    padding: 0 15px 5px 15px;
    font-weight: bold;
    color: #222;
  }
  .index-left-block ul {
    padding: 10px 15px;
  }
  .index-left-block li {
    padding: 5px;
  }
  .index-board-list {
    overflow: hidden;
  }
  .index-board-item {
    float: left;
    width: 400px;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
    padding: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
  }
  .index-board-item-inner {
    min-height: 125px;
    padding-left: 120px;
  }
  .index-board-car .index-board-item-inner{
    background: url(../assets/images/1.png) no-repeat;
  }
  .index-board-loud .index-board-item-inner{
    background: url(../assets/images/2.png) no-repeat;
  }
  .index-board-earth .index-board-item-inner{
    background: url(../assets/images/3.png) no-repeat;
  }
  .index-board-hill .index-board-item-inner{
    background: url(../assets/images/4.png) no-repeat;
  }
  .index-board-item h2 {
    font-size: 18px;
    font-weight: bold;
    color: #000;
    margin-bottom: 15px;
  }
  .line-last {
    margin-right: 0;
  }
  .index-board-button {
    margin-top: 20px;
  }
  .lastest-news {
    min-height: 512px;
  }
  .hot-tag {
    background: red;
    color: #fff;
  }
  .new-item {
    display: inline-block;
    width: 230px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



